import React from 'react';
import About from './Components/About/index.js';
import Frost from './Components/FrostedGlass/index.js';
import ProfileImg from './Components/ProfileImg/index.js';
import RefreshMsg from './Components/RefreshMsg/index.js';
import ScrollMsg from './Components/ScrollMsg/index.js';
import Skills from './Components/Skills/index.js';
import BackdropFilter from "react-backdrop-filter";
import 'animate.css';
import './App.css'
import { isElement } from 'react-dom/cjs/react-dom-test-utils.development';

class App extends React.Component {

  componentDidMount = () => {
    window.addEventListener('scroll', function(){
      if (window.pageYOffset < window.innerHeight / 2) {
        document.getElementById("back").style.filter = `blur(${window.pageYOffset / (window.innerHeight/2) * 10}px)`;
      } else {
        document.getElementById("back").style.filter = "blur(10px)";
      }
    });
  };

  // handleScroll = () => {
  //   console.log("what");
  //   // const { top } = this.wrapRef.getBoundingClientRect();
  //   if (top > 0 && top < 10 && !this.state.isActive) {
  //     this.setState({ isActive: true });
  //   }
  //   if ((top <= 0 || top >= 10) && this.state.isActive) {
  //     this.setState({ isActive: false });
  //   }
  // }

  render() {
    return (
      <div className="root" id="root">
        <div className="background animate__animated animate__fadeIn animate__delay-1s" id="back"></div>
        <div className="title-back">
          <RefreshMsg/>
          <Frost/>
          <ScrollMsg/>
        </div>
        <div className="intro animate__animated animate__fadeIn animate__delay-3s">
          <ProfileImg/>
          <About/>
          {/* <Skills/> */}
        </div>
      </div>
    );
  }
}

export default App;
